import React, { useState } from "react";
import Helmet from "../commponents/Helmet/Helmet";
import styles from "../styles/login.module.css";
import {useNavigate } from "react-router-dom";
import { LoadingButton } from "@mui/lab";
import { SERVER_URL } from "../Domain";
import axios from "axios";
const Validation = () => {
    const [data, setData] = useState({
        email: "",
        OTP: "",
    })
    const [error, setError] = useState("")
     const navigate = useNavigate();
    const [success, setSuccess] = useState("")
    const [isVerifEmail,setisVerifEmail] = useState(true)
    const [loading,setisloading] = useState(false)
    const handleChange = ({ currentTarget: input }) => {
        setData({ ...data, [input.name]: input.value })
        if(data.email.endsWith("@gmail.com")){
            console.log("hellooo")
        }
    }

    const handleSubmit = (e) => {
        setisloading(true)
        e.preventDefault()
        axios({
        method: "post",
        url: `${SERVER_URL}/api/user/forgetpassword`,
        withCredentials: true,
        data: data,
      }).then(({data})=>{
         setisloading(false)
        if(data.error){
            setError(data.error)
            setSuccess("")
        }
        if(data.message){
         setSuccess(data.message)
         setError("")
         setisVerifEmail(false)
        }
      }).catch((error)=>{
         setisloading(false)
        console.log(error)
      })
    }
        const handleSubmitOTP = (e) => {
             setisloading(true)
        e.preventDefault()
        axios({
        method: "post",
        url: `${SERVER_URL}/api/user/verifyOTP`,
        withCredentials: true,
        data: data,
      }).then(({data})=>{
         setisloading(false)
        if(data.error){
            setError(data.error)
            setSuccess("")
        }
        if(data.message){
         setSuccess(data.message)
         setError("")
         setisVerifEmail(false)
             setTimeout(()=>{
        // window.location="/login"
        navigate(`/change-password/${data.email}`)
    },2000)
        }
      }).catch((error)=>{
         setisloading(false)
        console.log(error)
      })
    }
    return (
        <Helmet title={"Resetting password"}>
            <div className={styles.container_login}>
                <div className={styles.login_form_container}>
                    <div className={styles.left}>
                        <form onSubmit={!data.OTP ? handleSubmit : handleSubmitOTP} className={styles.form_container}>
                            <h1>Ckecking  User</h1>
                            <input
                                type="email"
                                placeholder="Verify your email"
                                name="email"
                                value={data.email}
                                required
                                className={styles.input}
                                onChange={handleChange}
                                disabled={!isVerifEmail}
                            />
                            <input
                                type="text"
                                placeholder="Verify OTP from your Email"
                                name="OTP"
                                value={data.OTP}
                                required
                                disabled={isVerifEmail}
                                className={styles.input}
                                onChange={handleChange}
                            />
                            {
                                error && <div className={styles.error_msg}>{error}</div>
                            }
                            {
                                success && <div className={styles.success_msg}>{success}</div>
                            }
                            {
                                isVerifEmail ? 
                                (<LoadingButton loading={loading} sx={{ padding: 0,width:"10rem",margin:0 }} ><button type="submit" className={styles.green_btn}>Verify email</button></LoadingButton>):
                                (<LoadingButton loading={loading} sx={{ padding: 0,width:"10rem",margin:0 }} ><button type="submit" className={styles.green_btn}>Verify OTP</button></LoadingButton>)
                            }
                            
                        </form>
                    </div>
                    <div className={styles.right}>
                        <h1>Ticks:</h1>
                        <ol>
                            <li>1. Ckeck ur email to get the validation code sent to your email</li>
                            <li>2. Put the Varidation code to precced</li><br/>
                            <p>After accepting all u will be redirecting to the changing password</p>
                        </ol>
                    </div>
                </div>
            </div >
        </Helmet >
    )
}
export default Validation;